import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,App } from 'ionic-angular';

/**
 * Generated class for the HdashDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-hdash-details',
  templateUrl: 'hdash-details.html',
})
export class HdashDetailsPage {
  place: any;
  image:any;
  des:any;
  imagepath:any;


  constructor(public navCtrl: NavController, public navParams: NavParams,public app:App) {

    this.place = navParams.get('value');
    console.log( this.place);
    this.imagepath=navParams.get('ipath');
    this.des=this.place .notice_description;
    this.image=this.place.notice_photo;
    
    console.log(this.image) 
    console.log(this.des) 

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HdashDetailsPage');
  }

}



